:root {
  --bg-color-thead: #f1f3f6;
}

.k-pdf-export {
  font-family: "Roboto", sans-serif !important;
  margin-bottom: 0;
}

.k-pdf-export .pdf-header,
.pdf-header {
  /* align-items: center; */
  /* display: flex; */
  /* flex-direction: column; */
  /* justify-content: center; */
  margin-bottom: 10px;
  /* text-align: center; */
}

.k-pdf-export .pdf-header h3 {
  font-size: 20px !important;
  margin-bottom: 0 !important;
}

.k-pdf-export .filter h5 {
  margin-bottom: 5px !important;
}

.k-pdf-export .filter {
  margin-bottom: 0.5em;
}

.k-pdf-export .pdf-footer {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
}

.k-pdf-export .pdf-footer span {
  color: rgba(153, 102, 102, 0.897);
  font-size: 8px !important;
  margin-bottom: 5px;
}

.k-pdf-export .pdf-footer .line,
.pdf-footer .line {
  background: #e5e2e2;
  height: 1px;
  margin: 3em 0 1em;
  position: relative;
  width: 100%;
}

.k-pdf-export table thead > tr > th {
  background-color: var(--bg-color-thead);
  color: #957878;
  font-size: 16px;
}

.k-pdf-export table thead > tr > th,
.k-pdf-export table tbody > tr > td {
  /* overflow-wrap: break-word !important; */
  padding: 3px 15px !important;
  text-align: left;
  /* white-space: normal !important; */
  /* word-wrap: break-word !important; */
}

.k-pdf-export table thead > tr:first-child > th:first-child {
  border-top-left-radius: 0;
  font-weight: bolder;
}

.k-pdf-export table thead > tr:first-child > th:last-child {
  border-top-right-radius: 0;
  font-weight: bolder;
}

.k-pdf-export .ant-divider-horizontal.ant-divider-with-text-left:before {
  width: 0% !important;
}

.k-pdf-export .ant-divider-horizontal.ant-divider-with-text-left:after {
  width: 0% !important;
}
