/* Styles go here */
.page-header,
.page-header-space {
  height: 100px;
}

.page-footer,
.page-footer-space {
  height: 80px;
}

.page-header-transaction,
.page-header-transaction-space {
  height: 130px;
}

.page-header,.page-header-transaction,.page-footer {
  position: fixed;
  padding-top: 1em;
  padding-left: 0;
  padding-right: 0;
  width: 100%;
  text-align: center;
  /* border-top: 1px solid black;
  background: yellow; */
}

.page-footer {
  bottom: 0;
}

.page-header,.page-header-transaction {
  top: 0mm;
}

.print-transaction {
  font-size: 12px;
}

.page {
  page-break-after: always;
}

@page {
  margin: 20mm;
}

@media print {
  /* div, span {
    page-break-inside: avoid;
  } */

  .thead-header {
    display: table-header-group;
  }
  .table-content {
    width: 100%;
    margin: 0;
  }
  .tfoot-footer {
    display: table-footer-group;
  }

  .ant-table-body > table {
    page-break-inside: auto;
  }

  .ant-table-body > table > tr {
    page-break-inside: avoid;
    page-break-after: auto;
  }

  body {
    margin: 0;
    counter-reset: page_number;
  }

  #current-page:after {
    counter-increment: page_number;
    content: counter(page_number);
  }

  .avoid-page-break {
    page-break-before: auto;
    page-break-inside: avoid;
    page-break-after: auto;
  }
}
